@import url('https://cdn.jsdelivr.net/npm/swiper/swiper-bundle.min.css');

// Demo styles

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
